.form {
	margin: 0 auto;
	padding-top: 40px;
	padding-bottom: 104px;
}

@media screen and (min-width: 768px) {
	.form {
		width: 608px;
		padding: 60px 80px 40px 80px;
		background-color: var(--modal-bg);
		box-shadow: 7px 4px 14px rgba(0, 0, 0, 0.11);
		border-radius: 40px;
	}
}

@media screen and (min-width: 1280px) {
	.form {
		width: 618px;
		padding: 60px 80px;
	}
}

.title {
	text-align: center;
	font-weight: 700;
	font-size: 24px;
	line-height: 1.38;
	letter-spacing: 0.04em;
	margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
	.title {
		font-weight: 500;
		font-size: 36px;
		line-height: 1.36;
	}
}

.fieldContainer {
	position: relative;
	margin-bottom: 30px;
}

.input {
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 40px;
	background: var(--bg);
	border: 1px solid rgba(245, 146, 86, 0.5);
	border-radius: 40px;
	padding-left: 14px;
	padding-right: 14px;
	transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
	color: var(--color-main);
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
	box-shadow: 0 0 0 30px var(--color-input) inset;
	border: 1px solid var(--color-border-input);
	-webkit-text-fill-color: var(--color-main);
}
.inputBox {
	position: relative;
	display: block;
	margin-bottom: 30px;
}
.inputBox .input {
	margin-bottom: 30px;
}
.toggle {
	position: absolute;
	top: 2px;
	right: 10px;
	width: 34px;
	height: 34px;
	background-position: center;
	background-repeat: no-repeat;
	background-size: 24px 24px;
	border-radius: 50%;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
}
@media screen and (min-width: 768px) {
	.input {
		min-height: 52px;
		padding-left: 32px;
		padding-right: 30px;
	}
	.toggle {
		top: 8px;
	}
}
.validation {
	position: absolute;
	top: 105%;
	left: 0;
	right: 0;
	background: var(--bg);
	border: 1px solid rgba(245, 146, 86, 0.5);
	padding: 10px;
	border-radius: 20px;
	opacity: 0;
	z-index: -1;
	transition: var(--transition);
}
.validation.show {
	opacity: 1;
	z-index: 1;
}
.items {
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 8px;
}
.items li {
	position: relative;
	transition: var(--transition);
}
.items li.valid {
	color: var(--color-another);
}
.items li::before {
	content: "";
	width: 20px;
	height: 10px;
	display: inline-flex;
	background-color: transparent;
	background-position: center;
	background-repeat: no-repeat;
	background-size: 10px 10px;
	background-image: url(../../images/svg/radio-checked.svg);
}
.items li.valid::before {
	background-image: url(../../images/svg/edit-ok.svg);
}

.input::placeholder {
	font-weight: 400;
	font-size: 14px;
	line-height: 1.36;
	letter-spacing: 0.04em;
	color: var(--color-date);
}

@media screen and (min-width: 768px) {
	.input::placeholder {
		font-size: 18px;
		line-height: 1.39;
	}
}

.input:focus {
	outline: none;
	border: 1px solid var(--color-hover-active);
}

.questionText {
	font-weight: 400;
	font-size: 12px;
	line-height: 1.33;
	text-align: center;
	letter-spacing: 0.04em;
	color: var(--color-date);
}

.loginLink {
	color: #3091eb;
}

.buttonContainer {
	margin-bottom: 40px;
}

.button {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding: 0 28px;
	width: 100%;
	height: 44px;
	font-size: 20px;
	letter-spacing: 0.04em;
	border: 2px solid #f59256;
	border-radius: 40px;
	transition: var(--transition);
	color: var(--color-main);
}

.buttonDefault {
	background-color: var(--btn-bg-default);
}

.buttonActive {
	background-color: var(--btn-bg-another);
	color: var(--btn-color-hover-active);
}

.button:hover {
	background-color: var(--btn-bg-another);
	color: var(--btn-color-hover-active);
	cursor: pointer;
}

.register {
	margin-bottom: 16px;
}

.back {
	display: none;
}

@media screen and (min-width: 768px) {
	.back {
		display: block;
	}
}
